import { income_tax } from "./incometax.cul?cul_scope_id=1&cul_parent_scope_id=0";
export { income_tax };

import { taxpayer_gross_salary, taxpayer_proportion } from "./taxpayers.cul?cul_scope_id=2&cul_parent_scope_id=0";
export { taxpayer_gross_salary, taxpayer_proportion };

import { range } from 'underscore';

export const zero = ({ zero_in }) => zero_in;

export const revenue = ({ zero_in }) =>
zero({ zero_in }) +
range(0, 10).reduce(
(p, c) =>
income_tax({
  gross_salary_in: taxpayer_gross_salary({ taxpayer_id_in: c }) }) *

taxpayer_proportion({ taxpayer_id_in: c }) +
p,
0);